body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
.page-heading{
  font: 400 35px/1.5 Helvetica, Verdana, sans-serif;
}
.tree-container{
    background: white;
    height: 100vh;
    padding-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.container {
    height: 7vh;
    width: 70vw;
    background: #cce9ff;
    margin: 15px;
    display: flex;
    align-items: center;
    padding-left: 20px;
    cursor: pointer;
    font: 400 20px/1.5 Helvetica, Verdana, sans-serif;
    border: solid #8080804d 1px;
    border-radius: 5px;
    justify-content: space-between;
}
.container:hover{
    background: #2b96e8;
}
.container:hover .progress  {
    color: white;
}
.container:hover .concepts  {
    color: white;
}
.progress{
    font: 600 15px/1.5 Helvetica, Verdana, sans-serif; 
    color: grey;
    margin-right: 20px;
}
.concepts {
    font: 600 15px/1.5 Helvetica, Verdana, sans-serif; 
    color: grey;
    margin-right: 10px;
}
@media only screen and (max-width: 400px) {
    .container {
        font: 200 15px/1.5 Helvetica, Verdana, sans-serif;
    }
  }
.level-two-container{
    display: flex;
    align-items: center;
    padding-left: 40px;
    cursor: pointer;
    height: 8vh;
}
ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
  }
   
  li {
    font: 200 15px/1.5 Helvetica, Verdana, sans-serif;
    padding: 5px;
  }
      
  li a {
    text-decoration: none;
    color: #000;
    -webkit-transition: font-size 0.3s ease, background-color 0.3s ease;
    -webkit-transition: font-size 0.3s ease, background-fo 0.3s ease;
    transition: font-size 0.3s ease, background-fo 0.3s ease;
  }
   
  li a:hover {
    font-size: 30px;
    background:white;
  }
  .bullet-image{
      width: 30px;
      height: 30px;
      margin-right: 10px;
  }
  .transition-div{
    -webkit-transition: 0.5s;
    -webkit-transition: opacity 1s ease-out;
    transition: opacity 1s ease-out;
  }
  @media only screen and (max-width: 400px) {
    li {
        font: 200 10px/1.5 Helvetica, Verdana, sans-serif;
    }
  }
